.loading-box {
  width: 8em;
  height: 8em;
  position: fixed;
  top: 0;
  right: 0;
  left: 0;
  bottom: 0;
  margin: auto; }
  .loading-box .progress {
    position: absolute;
    width: 6em;
    height: 6em;
    background-color: white;
    border-radius: 50%;
    left: 1em;
    top: 1em;
    line-height: 6em;
    text-align: center; }
  .loading-box .left, .loading-box .right {
    width: 4em;
    height: 8em;
    overflow: hidden;
    position: relative;
    float: left; }
  .loading-box .left {
    border-radius: 8em 0 0 8em; }
  .loading-box .right {
    border-radius: 0 8em 8em 0; }
  .loading-box .left:after, .loading-box .right:after {
    content: "";
    position: absolute;
    display: block;
    width: 4em;
    height: 8em;
    background-color: white;
    border-radius: 8em 0 0 8em;
    background-color: #00C8FB; }
  .loading-box .right:after {
    content: "";
    position: absolute;
    display: block;
    border-radius: 0 8em 8em 0; }
  .loading-box .left:after {
    -webkit-animation: rotateA 2s infinite linear;
    -webkit-transform-origin: right center;
            transform-origin: right center; }
  .loading-box .right:after {
    -webkit-animation: rotateB 2s infinite linear;
    -webkit-transform-origin: left center;
            transform-origin: left center; }

@-webkit-keyframes rotateB {
  25%, 50% {
    -webkit-transform: rotateZ(180deg);
            transform: rotateZ(180deg); }
  75%, 100% {
    -webkit-transform: rotateZ(360deg);
            transform: rotateZ(360deg); } }

@-webkit-keyframes rotateA {
  0%, 25% {
    -webkit-transform: rotateZ(0deg);
            transform: rotateZ(0deg); }
  50%, 75% {
    -webkit-transform: rotateZ(180deg);
            transform: rotateZ(180deg); }
  100% {
    -webkit-transform: rotateZ(360deg);
            transform: rotateZ(360deg); } }

